        graph.restoreSubgraphNodes(groupNode,
                                   groupNode.subgraphNodes,
                                   groupNode.text);

        graph.restoreEdgesToFromSubgraph(graph.nodes[groupNodeId].subgraphNodes);

        graph.copyEdgeModsToTerminalestNode(groupNode);

